import React from 'react';
import LogoImg from 'common/static/Logo.svg';
import { HomePageTemplate } from 'common/components';
import classes from './About.module.scss';

export const About = () => {
  return (
    <HomePageTemplate>
      <div className={classes.about}>
        <h1>Quem somos?</h1>

        <div className={classes.card}>
          <div className={classes.row}>
            <div>
              <p>
                O Brasil é o quinto país com maior extensão territorial do
                mundo, com cerca de 152,5 milhões de hectares (17,9% do
                território brasileiro) sendo terras e áreas agricultáveis.
                Segundo o Sistema Nacional de Cadastro Rural (SNCR), em 2013 o
                país possuía cerca de 5.6 milhões de imóveis rurais, ocupando
                60% da área todal do Brasil.
              </p>
              <p>
                Analisando e estudando como é feito atualmente a compra e venda
                desses imóveis, a ideia da Terrafinder é facilitar esse processo
                que hoje é feito muitas vezes de maneira rudimentar e por meios
                como jornais, contatos pessoais, exploração e alguns canais
                digitais.
              </p>
              <p>
                Com uma onda de digitalização que hoje impacta até as gerações
                mais velhas, a ideia é construir um sistema que atenda as
                necessidades do público mais velho, mas que também dê liberdade
                para gerações mais novas poderem utilizar.
              </p>
              <p>
                A Terrafinder é uma startup que busca conectar e facilitar a
                vida de proprietários de imóveis rurais e de interessados em
                adquirir tais imóveis. Através de uma plataforma digital a ideia
                é permitir aos proprietários desses imóveis, anunciarem e
                promover suas vendas com facilidade e visibilidade para pessoas
                que tem interesse em comprar tais propriedades.
              </p>
            </div>

            <div>
              <img src={LogoImg} alt="" />
            </div>
          </div>

          <div className={classes.aboutValues}>
            <h2>Missão</h2>
            <p>Conectar compradores e vendedores de imóveis rurais</p>

            <h2>Visão</h2>
            <p>Ser uma plataforma completa de soluções para o agrobusiness</p>

            <h2>Valores</h2>
            <ul>
              <li>Integridade</li>
              <li>Lealdade</li>
              <li>Compromisso</li>
              <li>Smart Work</li>
              <li>União e Equipe</li>
            </ul>
          </div>
        </div>
      </div>
    </HomePageTemplate>
  );
};
